.header {
    .first-line {
        display: grid;
        grid-template-columns: 50% 50%;
        padding: 16px;

        .logo-col {
            display: flex;

            .link {
                display: flex;
                align-items: center;
                font-family: Roboto;
                font-size: 16px;
                font-weight: bold;
                color: #c3c6c9;
                text-decoration: none;
                line-height: 18px;

                .logo {
                    max-height: 66px;
                    align-items: center;
                }

                p {
                    margin-left: 5px;

                    span {
                        display: block;
                    }
                }
            }
        }

        .search-col {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            position: relative;

            input {
                background: #e6e6e6;
                color: #757575;
                font-size: 14px;
                line-height: 32px;
                border: none;
                padding-left: 16px;
                padding-right: 36px;
                box-sizing: border-box;
            }

            .icon {
                position: absolute;
                right: 16px;
                color: #757575;
                line-height: 34px;
            }

            #btn-search {
                font-size: 30px;
                font-size: 18px;
                left: 0;
                display: none;
            }

            #bars {
                right: 0;
                display: none;
            }
        }
    }

    .nav {
        background: #333333;
        font-family: roboto;

        .menu {
            display: flex;

            >.menu-item {
                display: inline-block;
                padding-left: 13px;
                padding-right: 13px;
                border-top: 5px solid #333333;
                transition: 0.2s all;
                position: relative;

                &:hover {
                    border-top: 5px solid #4eb7cd;
                    transition: 0.2s all;

                    .sub-menu {
                        opacity: 1;
                        transition: 0.2s all;
                        display: block;
                    }
                }

                a {
                    color: white;
                    text-decoration: none;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 43px;
                    display: inline-block;

                    .icon {
                        color: #4eb7cd;
                    }
                }

                &.current-menu-item {
                    border-top: 5px solid #4eb7cd;
                }
            }

            .sub-menu {
                position: absolute;
                background: #5a6378;
                border-radius: 2px;
                padding-top: 5px;
                padding-bottom: 5px;
                border: 0;
                left: 0;
                opacity: 0;
                transition: 0.2s all;
                display: none;

                .menu-item {
                    padding-top: 2px;
                    padding-bottom: 5px;

                    a {
                        color: #a5adbe;
                        padding-left: 20px;
                        padding-right: 20px;
                        white-space: nowrap;
                        box-sizing: border-box;
                        width: 100%;
                        line-height: 20px;
                        transition: 0.2s all;
                        padding-top: 2px;
                        padding-bottom: 5px;

                        &:hover {
                            color: #4eb7cd;
                            transition: 0.2s all;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 768px) {
    body:not(.page-home) .header {
        border-bottom: 1px solid #757575;
    }

    .header .first-line {
        grid-template-columns: 75% 25%;
    }

    body.with-special-header .header {
        display: none;
    }
}

@media (max-width: 768px) {
    .header .first-line .logo-col .link {
        font-size: 10px;
        line-height: 11px;
        color: #393f4f;
        font-weight: normal;
    }
}

@media (max-width: 768px) {
    .header .first-line .logo-col .link .logo {
        max-height: 43px;
    }
}

@media (max-width: 768px) {
    .header .first-line .search-col form {
        display: none;
    }
}

@media (max-width: 768px) {
    .header .first-line .search-col .icon {
        display: inline-block;
        margin-right: 10px;
    }
}

@media (max-width: 768px) {
    .header .first-line .search-col #btn-search {
        display: inline-block;
    }
}

@media (max-width: 768px) {
    .header .first-line .search-col #bars {
        display: inline-block;
    }
}

@media (max-width: 768px) {
    .header .nav {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        overflow: auto;
        z-index: 999;
    }
}

@media (max-width: 768px) {
    .header .nav .menu {
        display: block;
    }
}

@media (max-width: 768px) {
    .header .nav .menu>.menu-item:hover {
        border-top: 5px solid #333333;
    }
}

@media (max-width: 768px) {
    .header .nav .menu>.menu-item {
        width: 100%;
    }
}

@media (max-width: 768px) {
    .header .nav .menu .sub-menu {
        position: relative;
        margin-left: -30px;
        margin-right: -32px;
    }
}

@media (max-width: 768px) {
    .header .nav .menu .sub-menu .menu-item {
        padding-left: 20px;
    }
}

@media (max-width: 768px) {
    .header .nav .menu .sub-menu .menu-item a {
        white-space: normal;
    }
}

/* Nav mobile */

#mobileNav {
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    background: #393f4f;
    position: fixed;
    top: 0;
    z-index: 10;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
    transform: translateX(100%);
    -webkit-transition: all .4s linear;
    -o-transition: all .4s linear;
    transition: all .4s linear;

    .top-part {
        height: 73px;
        border-bottom: 3px solid rgba(255, 255, 255, 0.48);
        padding: 0 16px;

        p {
            font-size: 20px;
            color: #fff;
        }
    }

    #close-nav {
        width: 14px;
        height: 14px;
        display: block;
        padding: 0;
        margin: 0;

        span {
            display: block;
            width: 18px;
            height: 2px;
            background: #ffffff;

            &:first-child {
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
                position: relative;
                top: 1px;
                left: -3px;
            }

            &:last-child {
                -webkit-transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
                transform: rotate(-45deg);
                position: relative;
                top: -1px;
                left: -3px;
            }
        }
    }

    .nav-links {
        padding: 0 16px;

        >li {
            border-bottom: 1px dashed rgba(255, 255, 255, 0.6);
            padding: 25px 0;

            &.has-sub-menu {
                height: 65px;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;

                &.show-sub-menu {
                    height: auto;
                }

                .extra-holder {
                    height: 100%;
                }
            }

            &:last-child {
                border: 0;
            }

            font-size: 14px;
            font-weight: 500;
            color: #fff;
            text-decoration: unset;

            a {
                font-size: 14px;
                font-weight: 500;
                color: #fff;
                text-decoration: unset;
            }
        }
    }

    .nav-triangle {
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: #4eb7cd transparent transparent transparent;
        -webkit-transition: all .2s linear;
        -o-transition: all .2s linear;
        transition: all .2s linear;
    }

    .show-sub-menu .nav-triangle {
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
    }

    .nav-links .sub-menu li {
        padding: 10px 5px;

        &:first-child {
            padding-top: 25px;
        }

        a {
            display: block;
        }
    }

    &.opened {
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);
    }

    .sub-menu {
        height: 0;
        overflow: hidden;
    }

    .show-sub-menu .sub-menu {
        height: auto;
    }
}

