/* Header con breadcrum */

#sectionHeader {
    height: 72px;
    padding: 0 16px;
    -webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.24), 0 0 4px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.24), 0 0 4px 0 rgba(0, 0, 0, 0.12);
    background-color: #f5f5f5;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;

    >.extra-holder {
        height: 100%;

        p {
            color: #212121;
        }
    }

    #open-nav span {
        width: 18px;
        height: 2px;
        background: #212121;
        margin-bottom: 3px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

@media (max-width: 920px) {
    .header .nav .menu>.menu-item {
        padding-left: 7px;
        padding-right: 7px;

        a {
            font-size: 13px;
        }
    }
}